import { createRouter, createWebHistory } from 'vue-router'; // Użyj odpowiednich elementów z vue-router

import Home from '@/views/Home.vue';
import Uslugi from '@/views/Uslugi.vue';


const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/uslugi',
    name: 'Uslugi',
    component: Uslugi
  }
  // Dodaj inne trasy, jeśli są
];

const router = createRouter({
  history: createWebHashHistory(), // Możesz użyć innych trybów historii
  routes
});

export default router;